import React from 'react';
import classNames from 'classnames';

import { Heading, Text } from '../Typography';
import type { HeadingProps } from '../Typography/types';

import styles from './Step.module.scss';

export interface StepProps {
    index: number;
    title: string;
    description?: string;
    children?: React.ReactNode;
    inverted?: boolean;
    titleAs?: HeadingProps['as'];
}

const Step = ({
    index,
    title,
    description,
    children,
    inverted,
    titleAs = 'h4',
}: StepProps) => {
    const classList = classNames(styles.step, {
        [styles['step--inverted']]: inverted,
    });

    return (
        <li className={classList}>
            <div className={styles['step__index-container']}>
                <div className={styles.step__index}>
                    <Text size="large" weight="600" variant="line">
                        {index + 1}
                    </Text>
                </div>
            </div>
            <div className={styles['step__content-container']}>
                {title && (
                    <Heading
                        variant="h4"
                        as={titleAs}
                        className={styles.step__title}
                    >
                        {title}
                    </Heading>
                )}

                {description && (
                    <Text
                        weight="400"
                        variant="p"
                        className={styles.step__description}
                    >
                        {description}
                    </Text>
                )}
                {children}
            </div>
        </li>
    );
};

export default Step;
